	module.exports = {"data":{"markdownRemark":{"html":"<p>自分用メモを兼ねて、とりあえず、覚えてなきゃ話にならんステートメントをいくつか。</p>\n<ul>\n<li><code>while(式1) 式2</code>: 式1が正しい間式2を実行し続ける</li>\n<li><code>do 式1 while(式2)</code>: 一度式1を実行し、式2が正しい間式1を実行し続ける</li>\n<li><code>for(式1;式2;式3) 式4</code>: 式1で初期化し、式2が正しい間式3・4を実行し続ける</li>\n<li><code>if(式1) 式2 else 式3</code>: 式1が正しければ式2を、そうでなければ式3を実行</li>\n<li><code>break</code>: 一番内側のループから即座に抜ける</li>\n</ul>","frontmatter":{"cover":null,"date":"2003-12-04T19:22:00+09:00","strDate":"2003/12/4","title":"C/C++の基礎知識"}}},"pathContext":{"path":"/ja/blog/2003/12/04-ccの基礎知識/","langKey":"ja"}}

